import React from 'react'
import createApp from 'mickey'
import App from './App.jsx'

// 1. Initialize
const app = createApp()

// 2. Model
app.model({
  namespace: 'counter',
  state: { count: 0 },
  reducers: {
    increment: state => ({ count: state.count + 1 }),
    decrement: state => ({ count: state.count - 1 }),
  },
})

// 3. View
app.render(<App />, document.getElementById('root'))



