/* eslint-disable react/prop-types */

import React from 'react'
import { actions, connect } from 'mickey'
import './App.css'

const App = props => (
  <div id="counter-app">
    <h1>{props.count}</h1>
    <div className="btn-wrap">
      <button onClick={() => actions.counter.decrement()}>-</button>
      <button onClick={() => actions.counter.increment()}>+</button>
      <button style={{ width: 100 }} onClick={() => actions.app.incrementAsync()}>+ Async</button>
    </div>
  </div>
)

export default connect(store => ({ count: store.counter.count }))(App)



